'use client';
import React, { useState } from 'react';
import { 
  Save, 
  FolderOpen, 
  Undo, 
  Redo, 
  LayoutGrid, 
  Share2,
  Settings,
  Menu,
  ChevronDown
} from 'lucide-react';
import useStore from '@/store/useStore';

interface HeaderProps {
  title?: string;
}

const Header = ({ title = 'untitled' }: HeaderProps) => {
  const [workflowTitle, setWorkflowTitle] = useState(title);
  const { nodes, edges, loadWorkflow, undo, redo } = useStore();

  const handleSave = () => {
    const flow = { nodes, edges };
    const blob = new Blob([JSON.stringify(flow, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `${workflowTitle || 'workflow'}-${Date.now()}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const handleLoad = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (evt) => {
      try {
        const flow = JSON.parse(evt.target?.result as string);
        if (flow.nodes && flow.edges) {
            loadWorkflow(flow);
        } else {
            alert('Invalid workflow file format');
        }
      } catch (err) {
        console.error("Failed to parse workflow", err);
        alert("Invalid workflow file");
      }
    };
    reader.readAsText(file);
    e.target.value = '';
  };

  return (
    <header className="h-16 bg-[#1a1a1e]/80 backdrop-blur-xl border-b border-white/5 flex items-center justify-between px-6 z-50 sticky top-0 shadow-lg shadow-black/5 support-backdrop-blur:bg-[#1a1a1e]/90">
      {/* Left Section - Logo & Title */}
      <div className="flex items-center gap-6">
        {/* Logo */}
        <div className="flex items-center gap-3 group cursor-pointer">
          {/* <div className="w-9 h-9 rounded-xl bg-gradient-to-br from-violet-600 to-indigo-600 flex items-center justify-center shadow-lg shadow-violet-500/20 group-hover:shadow-violet-500/30 transition-all duration-300">
            <LayoutGrid className="text-white w-5 h-5" />
          </div> */}
          <div className="flex flex-col">
            <h1 className="text-white font-bold text-lg leading-tight tracking-tight group-hover:text-violet-400 transition-colors bg-clip-text text-transparent bg-gradient-to-r from-white to-gray-400">
              Galaxy.ai
            </h1>
            {/* <span className="text-[10px] text-gray-500 font-medium tracking-wider uppercase">Workflow Builder</span> */}
          </div>
        </div>

        <div className="h-8 w-px bg-white/10 mx-2" />
        
        {/* Workflow Title */}
        <div className="group relative pl-2">
          <input
            type="text"
            value={workflowTitle}
            onChange={(e) => setWorkflowTitle(e.target.value)}
            className="
              bg-transparent 
              text-white font-medium text-sm 
              outline-none w-56 px-3 py-1.5 
              rounded-lg
              hover:bg-white/5 focus:bg-white/10 
              transition-all duration-200
              border border-transparent focus:border-white/10
              placeholder:text-white/20 placeholder:font-normal
            "
            placeholder="Untitled Workflow"
          />
          {/* <span className="absolute right-2 top-1/2 -translate-y-1/2 opacity-0 group-hover:opacity-100 transition-opacity text-gray-500 pointer-events-none">
            <Settings size={12} />
          </span> */}
        </div>
      </div>

      {/* Right Section - Actions */}
      <div className="flex items-center gap-3">
        {/* History Controls */}
        <div className="flex items-center gap-1 bg-white/5 p-1 rounded-lg border border-white/5 backdrop-blur-sm">
          <button 
            onClick={undo}
            className="p-1.5 text-gray-400 hover:text-white hover:bg-white/5 rounded-md transition-all active:scale-95"
            title="Undo (Ctrl+Z)"
          >
            <Undo size={14} strokeWidth={2.5} />
          </button>
          <button 
            onClick={redo}
            className="p-1.5 text-gray-400 hover:text-white hover:bg-white/5 rounded-md transition-all active:scale-95"
            title="Redo (Ctrl+Y)"
          >
            <Redo size={14} strokeWidth={2.5} />
          </button>
        </div>

        <div className="h-6 w-px bg-white/10 mx-1" />

        {/* Load Button */}
        <label className="flex items-center gap-2 px-3 py-2 text-xs font-semibold text-gray-300 hover:text-white hover:bg-white/5 rounded-lg transition-all cursor-pointer border border-transparent hover:border-white/10">
          <FolderOpen size={16} className="text-gray-400 group-hover:text-white" />
          <span>Load</span>
          <input 
            type="file" 
            className="hidden" 
            accept=".json" 
            onChange={handleLoad} 
          />
        </label>

        {/* Save Button */}
        <button 
          onClick={handleSave}
          className="
            flex items-center gap-2 px-4 py-2 
            bg-white text-black text-xs font-bold 
            rounded-lg 
            hover:bg-gray-100 active:scale-95 
            transition-all 
            shadow-[0_0_20px_rgba(255,255,255,0.1)] hover:shadow-[0_0_25px_rgba(255,255,255,0.2)]
          "
        >
          <Save size={16} />
          <span>Save Flow</span>
        </button>
      </div>
    </header>
  );
};

export default Header;
